
.footer {
	display: flex;
	flex-flow: nowrap;
	justify-content: space-between;
	padding: 84px 20px 60px;
}

.footer__menu_link {
	display: inline-block;
	margin-bottom: 16PX;
	font-weight: 400;
	color: #8993ad;
	transition: color 0.4s;
}

.footer__menu_link:hover {
	color: #ff2f96; 
}

.footer__social_conteiner {
	padding-right: 20px;
}

.contact__link {
	padding-right: 22px;
	padding-left: 16px;
	margin-bottom: 30px;
	font-size: 25px;
	font-weight: 400;
	color: #8993ad;
	vertical-align: middle;
	display: block;
	transition: color 0.4s;
}


.contact__link:hover {
	color: #ff2f96; 
}

.footer__button {
    border: 1.0px solid #ff2f96;
    border-radius: 2px;
    background: transparent;
	color: #ff2f96;
	transition: background 0.4s, color 0.4s;
	width: 250px;
	height: 45px;
	cursor: pointer;
}

.footer__button:hover {
	background: #ff2f96;
	color: #fff;
}

.social__list {
	margin-top: 30px;
	display: flex;
}

.social__icon {
 fill: #8993ad;
	width: 30px;
	height: 30px;
	transition: fill 0.4s;
}

.social__icon:hover {
    fill: #ff2f96;
	width: 30px;
	height: 30px;
}

.social__item {
	margin-right: 26px;
}

.copyright {
	background-color: #31293c;
	text-align: center;
	padding: 30px;
}

.copyright small {
	font-size: 16px; 
	color: #fff;
}




@media (max-width: 1024px) {
	.footer {
		padding: 104px 20px 63px;
	}
	.copyright small {
		font-size: 12px; 
	}

	.footer__social_conteiner {
		padding-right: 0;
	}
}





@media (max-width: 824px) {
	.footer {
		flex-flow: wrap;
	}

	.footer-contact, .footer-order-a-call {
		margin-bottom: 30px;
	}
	
	.footer__contact {
		color: #8993ad;
	}

	.footer__contact {
		font-size: 16px;
	}
}






@media (max-width: 425px) {
	.footer {
		flex-direction: column;
		padding: 46px 8px;
	}

	.footer__menu_link {
		font-size: 14px;
	}

	.footer__contact {
		padding-top: 30px;

	}

	.contact__link {
		font-size: 20px;
		margin-bottom: 22px;
		padding-left: 0px;
	}

	.footer__button {
		background-color: #ff4e2e;
		color: #fff;
		border: none;
		width: 280px;
		height: 57px;
	}

	.social__list {
		margin-top: 54px;
	}

	.copyright {
		padding: 14px;
	}
}
